import { render } from 'react-dom'
import React, { useState, useEffect, useRef  } from "react"
import styled from "styled-components"
import { useTransition, animated, config, useTrail, useSpring } from 'react-spring'
import Layout from "../components/common/layout/layout"
import SEO from "../components/common/layout/seo"
import Sub_Navigation from "../components/common/navigation/sub_navigation"


const SecondPage = () => {
    
  const [items, set] = useState([1,2,3])
  const transitions = useTransition(items, item => item.key, {
  from: { transform: 'translate3d(0,-40px,0)' },
  enter: { transform: 'translate3d(0,0px,0)' },
  leave: { transform: 'translate3d(0,-40px,0)' },
  })
  
    

  return  (

        <Layout>
        <SEO title="Page two" />
        <Sub_Navigation />
        <Container>
        {transitions.map(({ item, props, index }) =>
        <animated.div key={index} style={props}>{item.text}</animated.div>
        )}
         </Container>               
      </Layout>
    )
  }
  

  
  const Container = styled(animated.div)`
  display: grid;
  grid-template-columns: repeat(10, 1fr);
  
    div {
      height: 100vh;
      background: rgba(0, 110, 250, 0.5);
    }
  `
  const Masthead = styled(animated.h1)`
  position: absolute;
  top: 40%;
  left: 10%;
`
 
const GetStartedContainer = styled(Container)`
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding: 80px 0 40px;
`

const TryItButton = styled.button`
  font-weight: 500;
  font-size: 14px;
  color: white;
  letter-spacing: 1px;
  height: 60px;
  display: block;
  margin-left: 8px;
  text-transform: uppercase;
  cursor: pointer;
  white-space: nowrap;
  background: ${props => props.theme.color.secondary};
  border-radius: 4px;
  padding: 0px 40px;
  border-width: 0px;
  border-style: initial;
  border-color: initial;
  border-image: initial;
  outline: 0px;
  &:hover {
    box-shadow: rgba(110, 120, 152, 0.22) 0px 2px 10px 0px;
  }
  @media (max-width: ${props => props.theme.screen.md}) {
  }
  @media (max-width: ${props => props.theme.screen.sm}) {
    margin-left: 0;
  }
`

  export default SecondPage